.hamburger {
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
  color: #000;
  outline: 0;
  z-index: 20;
  position: fixed;
  right: 1rem;
}
.hamburger:hover {
  cursor: pointer;
}
.hamburger-box {
  width: 37px;
  height: 24px;
  display: inline-block;
  position: relative;
}
.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
}
.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 37px;
  height: 3px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
}
.hamburger-inner::before {
  top: -9px;
}
.hamburger-inner::after {
  bottom: -9px;
}
.hamburger--minus .hamburger-inner::before,
.hamburger--minus .hamburger-inner::after {
  transition: bottom 0.04s 0s ease-out, top 0.04s 0s ease-out, opacity 0s linear;
}
.hamburger--minus.is-active .hamburger-inner::before,
.hamburger--minus.is-active .hamburger-inner::after {
  opacity: 0;
  transition: bottom 0.04s ease-out, top 0.04s ease-out, opacity 0s 0.04s linear;
}
.hamburger--minus.is-active .hamburger-inner {
  transform: rotate(45deg);
}
.hamburger--minus.is-active .hamburger-inner::before {
  top: 0;
  transform: rotate(90deg);
  opacity: 1;
}
.hamburger--minus.is-active .hamburger-inner::after {
  bottom: 0;
}
